import React from 'react';
import styled from 'styled-components';

export const Venues = ({ data }) => (
  <>
    <h1>Upcoming</h1>

    {Venue(data).map((venue, i) => (
      <div key={i}>
        {/* <h1>{venue}</h1>
        {data[venue].events && <Events events={data[venue].events} />} */}
      </div>
    ))}
  </>
);

const Venue = events =>
  Object.keys(events).map(
    (venue: any) => events[venue].events.length > 0 && venue,
  );

const Events = ({ events }) => (
  <>
    {events.slice(0, 5).map((event, i) => (
      <Event key={i}>
        <h2>{event.title}</h2>
        <h2>{event.lineup}</h2>
        <h2>{event.time.replace(' / ', '')}</h2>
      </Event>
    ))}
  </>
);

const Event = styled.div`
  padding: 1rem 2rem;
  border: 2px solid rgba(255, 255, 255, 0.8);
  border-radius: 0.25rem;
  display: inline-block;
  margin: 1rem 20rem 1rem 5rem;

  h2 {
    font-size: 1rem;
  }
`;
